<template>
  <div class="container">
    <div id="go" class="table-content">
      <el-form
        :inline="true"
        size="mini"
        :model="searchInfo"
        class="demo-form-inline searchFrom"
        label-width="130px">
        <el-row>
          <el-col :span="10">
            <el-form-item label="事项名称">
              <el-input v-model="searchInfo.funName"
                clearable size="mini" placeholder="请输入事项名称"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="所属系统">
              <el-select v-model="searchInfo.funType" clearable size="mini"
                placeholder="请选择所属系统" style="width:100%">
                <el-option
                  v-for="item in funTypeArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <!-- <el-col :span="5">
            <el-form-item label="业务类型">
              <el-select v-model="searchInfo.businessType" clearable size="mini"
                placeholder="请选择业务类型">
                <el-option
                  v-for="item in businessTypeArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col> -->
          <el-col :span="5">
            <el-form-item label="规则状态">
              <el-select v-model="searchInfo.ruleStruts" clearable size="mini"
                placeholder="请选择规则状态" style="width:100%">
                <el-option
                  v-for="item in ruleStrutsArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5">
            <el-form-item label="是否接入流程">
              <el-select v-model="searchInfo.isBus" clearable size="mini"
                placeholder="请选择是否接入流程" style="width:100%">
                <el-option
                  v-for="item in isBusArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="作用域">
              <el-select v-model="searchInfo.funLevel" clearable size="mini"
                placeholder="请选择作用域" style="width:100%">
                <el-option
                  v-for="item in funLevelArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="审核类型">
              <el-select v-model="searchInfo.auFlag" clearable size="mini"
                placeholder="请选择审核类型" style="width:100%">
                <el-option
                  v-for="item in auFlagArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5" :offset="3">
            <el-form-item>
              <el-button type="primary" size="mini" @click="search()">查询</el-button>
              <el-button size="mini" @click="resetSearchCondition()">重置</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div class="tableDiv">
        <el-table
          class="customer-table"
          v-loading="loading"
          :data="tableData"
          :header-cell-style="{background:'#F2F2F2', textAlign:'left', border: 0}"
          style="width: 100%">
          <el-table-column
            prop="funName"
            label="事项名称">
          </el-table-column>
          <!-- <el-table-column
            prop="funType"
            label="事项类型">
            <template slot-scope="scope">
              <span>{{ scope.row.businessType | businessType }}</span>
            </template>
          </el-table-column> -->
          <el-table-column
            prop="funType"
            label="所属系统">
            <template slot-scope="scope">
              <span>{{ scope.row.funType | funType }}</span>
            </template>
          </el-table-column>
          <el-table-column width="150px"
            prop="ruleStruts"
            label="规则状态">
            <template slot-scope="scope">
              <span>{{ scope.row.ruleStruts | ruleStruts }}</span>
            </template>
          </el-table-column>
          <el-table-column width="150px"
            prop="isBus"
            label="是否接入流程">
            <template slot-scope="scope">
              <span>{{ scope.row.isBus | isBus }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="funLevel"
            label="作用域">
            <template slot-scope="scope">
              <span>{{ scope.row.funLevel | funLevel }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="areaName"
            label="地市区域">
            <template slot-scope="scope">
              <span>{{ scope.row.areaName ? scope.row.areaName : '一'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="auFlag"
            label="审核类型">
            <template slot-scope="scope">
              <span>{{ scope.row.auFlag | auFlag }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="isLeading"
            label="是否以柜面为准">
            <template slot-scope="scope">
              <span>{{ scope.row.isLeading | isLeading }}</span>
            </template>
          </el-table-column>
          <el-table-column width="100px"
            label="操作">
            <template slot-scope="scope">
              <el-button @click="editRule(scope.row)" type="text">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          style="margin-top:15px"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="searchInfo.page"
          :page-sizes="[10, 20]"
          :page-size="searchInfo.pageSize"
          layout="prev, pager, next, jumper, sizes"
          :total="pageSum">
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="规则编辑"
      :visible.sync="dialogVisible"
      @close="handleClose"
      width="60%">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px">
        <el-form-item label="事项名称：" prop="funName">
          <span>{{ ruleForm.funName }}</span>
        </el-form-item>
        <!-- <el-form-item label="事项类型：" prop="businessType">
          <el-select
            v-model="ruleForm.businessType"
            filterable
            placeholder="请选择事项类型"
            style="width: 85%">
            <el-option
              v-for="item in businessTypeArr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="所属系统：" prop="funType">
          <!-- <el-select class="pop_select" popper-class="pop_select_down" v-model="ruleForm.funType" @change="selectSys" multiple collapse-tags size="small" placeholder="请选择所属系统" style="width: 85%">
            <el-checkbox v-model="isSelectAll" style="padding-left:18px" @change="selectAll">全选</el-checkbox>
            <el-option
              v-for="item in funTypeArr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
              <span class="check"></span>
              <span>{{ item.label }}</span>
            </el-option>
          </el-select> -->
          <el-select v-model="ruleForm.funType" clearable
            placeholder="请选择所属系统">
            <el-option
              v-for="item in funTypeArr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否生效：" prop="ruleStruts">
          <el-switch
            v-model="ruleForm.ruleStruts"
            active-color="#13ce66">
          </el-switch>
        </el-form-item>
        <el-form-item label="是否接入流程：" prop="isBus">
          <el-switch
            v-model="ruleForm.isBus"
            active-color="#13ce66">
          </el-switch>
        </el-form-item>
        <el-form-item label="作用域：" prop="funLevel">
          <el-radio-group v-model="ruleForm.funLevel">
            <el-radio :label="item.value" :value="item.label" v-for="item in funLevelArr" :key="item.value">{{ item.label }}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="审核类型：" prop="auFlag">
          <el-radio-group v-model="ruleForm.auFlag">
            <el-radio :label="item.value" :value="item.label" v-for="item in auFlagArr" :key="item.value">{{ item.label }}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否以柜面为准：" prop="isLeading">
          <el-switch
            v-model="ruleForm.isLeading"
            active-color="#13ce66">
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmRule">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'auditConfigRule',
  data() {
    return {
      loading: false,
      searchInfo: { // 查询数据
        funName: null,
        funType: null,
        // businessType: null,
        ruleStruts: null,
        isBus: null,
        funLevel: null,
        auFlag: null,
        page: 1,
        pageSize: 10
      },
      pageSum: 0,
      dialogVisible: false,
      ruleForm: { // 弹窗数据
        id: null,
        orgId: null,
        funId: null,
        funName: null,
        // businessType: null, // 业务类型废弃
        // funType: [], // 系统类型多选改单选
        funType: null,
        ruleStruts: null,
        isBus: null,
        funLevel: null,
        auFlag: null,
        isLeading: null
      },
      isSelectAll: false,
      rules: {
        funType: [{required: true, message: '请选择所属系统', trigger: 'change'}],
        ruleStruts: [{required: true, message: '请选择是否生效', trigger: 'change'}],
        isBus: [{required: true, message: '请选择是否接入流程', trigger: 'change'}],
        funLevel: [{required: true, message: '请选择作用域', trigger: 'change'}],
        auFlag: [{required: true, message: '请选择审核类型', trigger: 'change'}],
        isLeading: [{required: true, message: '请选择是否以柜面为主', trigger: 'change'}]
      },
      funTypeArr: [
        {label: "菜单类型", value: '1'},
        {label: "统征经办系统", value: '2'},
        {label: "统征工伤系统", value: '3'},
        {label: "统征网报系统", value: '4'},
        {label: "城乡系统", value: '5'},
        {label: "开发运维一体化平台", value: '6'},
        {label: "稽核风控", value: '10'}
      ],
      // businessTypeArr: [
      //   {label: "参保征缴", value: '1'},
      //   {label: "工伤待遇", value: '2'}
      // ],
      ruleStrutsArr: [
        {label: "启动", value: '1'},
        {label: "停用", value: '2'}
      ],
      isBusArr: [
        {label: "是", value: "1"},
        {label: "否", value: "2"}
      ],
      funLevelArr: [
        {label: "省级", value: "1"},
        {label: "市级", value: "2"},
        // {label: "区级", value: "3"}
      ],
      auFlagArr: [
        {label: "自动审核", value: "0"},
        {label: "手动审核", value: "1"}
      ],
      tableData: []
    }
  },
  mounted() {
    this.getTableDate()
  },
  methods: {
    handleSizeChange(val) {
      this.loading = true
      this.searchInfo.pageSize = val
      this.getTableDate()
    },
    handleCurrentChange(val) {
      document.getElementById("go").scrollIntoView()
      this.loading = true
      window.scrollBy(0, -10)
      this.searchInfo.page = val
      this.getTableDate()
    },
    /**
     * 查询
     */
    search() {
      this.searchInfo.page = 1
      this.getTableDate()
    },
    /**
     * 重置
     */
    resetSearchCondition() {
      this.searchInfo = {
        funName: null,
        funType: null,
        // businessType: null,
        ruleStruts: null,
        isBus: null,
        funLevel: null,
        auFlag: null,
        page: 1,
        pageSize: 10
      },
      this.getTableDate()
    },
    /**
     * 获取事项规则列表
     */
    getTableDate() {
      this.$get("/sys/auditconfigrule/getAuditConfigRuleDTOListPage", this.searchInfo).then(res => {
        if (res.data.success) {
          this.tableData = res.data.result.dataList
          this.pageSum = res.data.result.totalCount
          this.loading = false
        } else {
          this.$message({
            type: "error",
            message: res.data.resultDes
          })
        }
      }).catch(error => {
        console.log(error)
      })
    },
    /**
     * 一一选中各市后，全选勾选
     */
    selectSys(val) {
      if (val.length === this.funTypeArr.length) {
        this.isSelectAll = true
      } else {
        this.isSelectAll = false
      }
    },
    /**
     * 全选
     */
    selectAll(val) {
      this.ruleForm.funType = []
      if (val) {
        this.funTypeArr.forEach(item => {
          this.ruleForm.funType.push(item.value)
        })
      } else {
        this.ruleForm.funType = []
      }
    },
    /**
     * 编辑，唤起弹窗，并获取规则详情
     */
    editRule(row) {
      this.dialogVisible = true
      this.getDetail(row)
    },
    /**
     * 获取规则详情
     */
    getDetail(row) {
      let params = {
        id: row.id
      }
      this.$get("/sys/auditconfigrule/getAuditConfigRuleInfo", params).then(res => {
        if (res.data.success) {
          Object.keys(res.data.result).forEach(key => {
            if (key in this.ruleForm) {
              this.ruleForm[key] = res.data.result[key]
            }
          })
          // 改变数据结构用以回填
          this.ruleForm.ruleStruts = res.data.result.ruleStruts == '1' ? true : false
          this.ruleForm.isBus = res.data.result.isBus == '1' ? true : false
          this.ruleForm.isLeading = res.data.result.isLeading == '0' ? true : false
        } else {
          this.$message({
            type: "error",
            message: res.data.resultDes
          })
        }
      }).catch(error => {
        console.log(error)
      })
    },
    /**
     * 编辑规则，保存
     */
    confirmRule() {
      this.$refs["ruleForm"].validate((valid) => {
        let params = {}
        Object.keys(this.ruleForm).forEach(key => {
          params[key] = this.ruleForm[key]
        })
        
        // 改变传参，以符合数据库保存数据的结构，此处注意用严格相等
        params.ruleStruts =this.ruleForm.ruleStruts === true ? '1' : '2'
        params.isBus = this.ruleForm.isBus === true ? '1' : '2'
        params.isLeading = this.ruleForm.isLeading === true ? '0' : '1'
        this.$post("/sys/auditconfigrule/updAuditConfigRuleInfo", params).then(res => {
          if (res.success) {
            this.dialogVisible = false
            this.getTableDate()
            this.$message({
              type: "success",
              message: '修改规则成功'
            })
          } else {
            this.$message({
              type: "error",
              message: res.resultDes
            })
          }
        }).catch(error => {
          console.log(error)
        })
      })
    },
    // 弹窗关闭
    handleClose() {
      this.isSelectAll = false
      this.ruleForm = {
        id: null,
        orgId: null,
        funId: null,
        funName: null,
        // businessType: null,
        // funType: [],
        funType: null,
        ruleStruts: null,
        isBus: null,
        funLevel: null,
        auFlag: null,
        isLeading: null
      },
      this.$refs["ruleForm"].resetFields();
    }
  }
}
</script>
<style lang="less" scoped>
.tableDiv {
  margin-top: 20px;
  .el-table__row>td {
    border-right: none;
    border-left: none
  }
}
.searchFrom /deep/ .el-form-item {
  display: flex;
}
.el-col {
  height: 48px;
}
.customer-table /deep/ th{
  border:none;
}
/deep/ .el-form-item {
  width: 100%;
}
/deep/ .el-form-item__content {
  width: 100%;
}
.customer-table /deep/ td,.customer-table /deep/ th.is-leaf {
  border:none;
  border-bottom: 1px solid #EBEEF5;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected::after {
  top: 1px;
  left: 24px;
  font-weight: 400;
  z-index: 1;
  color: #fff;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected .check {
  background-color: #409eff;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item .check {
  display: inline-block;
  position: relative;
  width: 15px;
  height: 15px;
  top: 3px;
  border: 1px solid #DCDFE6;
  margin: 0 6px 0 3px;
  border-radius: 2px;
  box-sizing: border-box;
  background-color: #FFF;
  z-index: 1;
  transition: border-color .25s cubic-bezier(.71,-.46,.29,1.46),background-color .25s cubic-bezier(.71,-.46,.29,1.46);
}
</style>


